
$FORM_EL_HEI: 55px;
#myForm {
  outline: 1px dashed red;
  box-shadow: 0px 0px 10px cornflowerblue;
  z-index: 0;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  padding: 10px;
  background-color: yellow;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .form_el {
    position: relative;
    z-index: 5;
    display: block;
    width: 100%;
    height: $FORM_EL_HEI;
    padding: 0 10px;
    border: 1px solid #333333;
    font-size: 16px;
    text-align: center;

    + .form_el {
      margin-top: 10px;
    }
  }

  .id_input,
  .pw_input {
    width: 300px;
    margin: 0 auto;
    color: #333;
    background-color: white;
  }

  .login_btn {
    width: 300px;
    margin: 0 auto;
    background-color: #333;
    color: white;
    font-weight: 700;
  }

  .join_btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    margin: 0 auto;
    background-color: #ddd;
    color: #333;
    font-weight: 700;
  }
}
