
.CompNumPad {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  > .bg {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .num_list {
    background-color: skyblue;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5px;
    box-sizing: border-box;
    padding: 10px;
    position: relative;
    z-index: 10;
    width: calc(100% - 20px);

    > button {
      background-color: rgba(0, 0, 0, 0.05);
      color: black;
      height: 58px;
      font-weight: 900;
      color: white;
    }
    > button:active {
      background-color: white;
      color: skyblue;
    }
  }
}
